  `閘門43:洞見,擅長以獨特並且有創意的方式來表達自己，對眾人有興趣的議題不見得喜歡，只對自己特定有興趣的領域一頭熱。代表是內在的耳朵，只聆聽自己獨特的聲音，只要在對的時機點上，你是有能力將這個獨特並且引發突變的覺察塑造成型。對你來說，最困難的莫過於傾聽，或真心聆聽別人所說的話，這是你與生俱來的遺傳因子，為了避免被外界影響所產生的自我保護作用。 `,
  `閘門44:警覺,人力資源經理的典型，可以嗅出別人的才華與潛力，伯樂。不管是在銷售、保衛或為家族而奮戰，你在每個當下皆能保持警覺，值得信賴。 `,
  `閘門45:收集者,著眼於家族或社群的未來與財富，家族領導者，最後同意新法規是否成立的人，以教育的方式來提升子民，贏得富足。具有很深的佔有欲，你是天生的權威和引領者，你並不是執行者，當你善加利用所擁有的一切，一切就能運轉順暢。 `,
  `閘門46:自我決心,如果放下期待，你就能在對的時間走到對的位置上，每個經驗都是完美的，對你來說都是對的經歷。你的存在就是個幸運兒，無論成敗皆取決於你的高我意志，這是一個抽象的過程，或許會激發你的潛能也或許帶來混亂。你所學的人生課題，以及與他人分享的智慧，皆源於你的決心、努力與全心投入於生活中的每一種體驗。 `,
  `閘門47:了解,根據過往的經驗來理解人生，常常回憶過往，腦中充滿影響，隨時自行想像拼貼中。面對每個出現的結論，不必感到有壓力而有所行動，只需單純在腦袋中享受各種排列的可能，直到某個答案鮮明跳出來，這時若有人問你，你才真正準備好可以跟大家分享你的領悟。 `,
  `閘門48:深度,有深度、有智慧，雖然常常質疑自己。智慧的泉源，要等待對的時機點才能與人分享。你最強烈的渴望就是想表達並分享你的深度，來協助大家了解、修正。耐心等待，才能吸引對的人來到你身邊，引發你將自身的深度顯現出來。 `,
  `閘門49:拒絕,你的原則可能引發你去拒絕別人，或者讓別人來拒絕你，此閘門是關於每段關係的基本原則，若有人不接受你的規則你會將他排除在外，這也是離婚與革命的閘門。那些得到你允許，可以進入你圈子裡的人必須願意支持你與你所設定的原則，其餘的人則被排除在外。你就是做決定的人，你來決定要接受或拒絕。但你也害怕被拒絕，也擔憂出乎意料的結果這會讓你緊張不已。 `,
  `閘門50:價值,建立或挑戰社會現有的價值與規範，探索責任的歸屬，內在對家族與社群具有深厚的責任感，常常為了社群的利益而奮鬥。守護家族的價值，你的任務是認清那些價值、規則，能保障族群所在意的安危。你本能的覺察力，能審視出敗壞與不公、不必要或利己，又或者是需要被挑戰與改變的地方，這就是你在家族中展現的關懷。 `,
  `閘門51:衝擊,為了成為最棒的，或是成為第一人，不斷考驗自己，非常具有競爭心，熱愛去做別人從未做過的事情，挑戰別人不敢的，超越極限。能承受衝擊，同時也會為別人帶來驚嚇，如此才能讓他們跳脫舒適圈，引導他人在個人層面有所超越。 `,
  `閘門52:靜止,按兵不動，靜待好時機，全力出擊，專注、專心，蓄勢待發，能待在同個地方，不偏不倚。在壓力之下，專注評估，這是一股專心的原始力量。一旦某件事情獲得你的認同，你則會驅動向前。 `,
  `閘門53:收縮,總覺得隨時都要有個新的開始，所以若身陷重複的困局中，就容易感到無聊或厭煩。你帶有一個不得不啟動新事物的動能，若你給自己充分的時間做出正確的決定，清楚在何處投入能量，當時機點來臨，你將能啟動新的循環，也會看到它開花結果。 `,
  `閘門54:野心,渴望追求物質層面的成功，要超前，要領先，從基層往上爬，對於賺錢有股莫名的驅動力，是一股不斷想前進的強烈驅動力。你內在渴望向上提升的衝勁，同時也為你周圍的人帶來蛻變的動力，藉以拓展出一片天，讓眾人能夠在平等的條件之下大展深受，你的動力需要方向，藉以導引你到有價值的人事物上。 `,
  `閘門55:精神,多愁善感，對音樂很敏銳，對聲音很敏感。非常浪漫，不管付出多少代價，都要尊重自己的情感，情緒高低起伏很正常，不要壓抑自己。容易受情緒週期憂鬱所影響，經常在希望與痛苦中循環。若你想獨處，不需要理由，只要單純尊重自己的感受，最深刻憂鬱的時候，也是最具創意的時刻。不管情緒如何起伏，都無須比較或被外人所影響。 `,
  `閘門56:刺激,運用過往的經驗來闡述你的想法與概念，講故事的人，對於引發聽眾的情緒非常有興趣。想法一旦以口語表達出來，就完成了整個過程，關於你想探索的新想法或心體驗，都深受你個人的感覺所影響，你的故事為他人增添豐富的色彩。 `,
  `閘門57:直覺的清晰,直覺能確保你是健康的，讓你生存無虞，對於聲音與振動頻率非常敏銳，具有強烈的直覺。如果你想知道對方真正想傳遞的言語，請善用本能，以右耳傾聽，你必須警覺並專注於當下，才能聽清楚直覺中心傳達給你的訊息，否則你可能會忽略。 `,
  `閘門58:活力,具有源源不絕的動力，渴望改善周圍的一切，對於挑戰現有的制約充滿活力。你最想滿足的渴望，就是能將一己之力，貢獻於有價值的事物之上。你富有同情心，有膽量去挑戰既定模式或背後的權威。 `,
  `閘門59:性,生產，複製，不論是生小孩或是生出新計畫，都很厲害，渴望在性的層面更加親密。關於性結合與創造新生命，運作我們在基因層面的策略。你唯一真正的選擇，必須透過你的權威來決定自己如何開始每一段親密關係。 `,
  `閘門60:接受,有潛力為世界帶來全新的改變，為此深感壓力。當你覺得無力改變周遭時，向內看，接受突變過程，因為你活在自己的世界中，相信內在蛻變正在發生，混亂中充滿潛能，這段過程當中若失去耐心，你內在的憂鬱則會加深。 `,
  `閘門61:神秘,腦中充滿壓力想解開未知的神秘，能帶來前所未有的突變洞見，偉大的思想家，熱愛未知，但也需要接受，有些事情無法被解釋，也無法被了解。當你知道自己具有獨特的見解，並且願意等待對的時間才表現，你會感到自由。當你的理解得以啟蒙並激勵他人，你會因此受到鼓舞。如果受限於強大壓力下，讓未知困擾你，你的靈感可能會產生錯覺，進而感到困惑，並且陷入不確定感與焦慮之中。記住!!享受你的思維，讓內心的覺知在對的時機展現。 `,
  `閘門62:細節,能夠著眼於細節，總是需要知道實際上到底發生什麼事，能夠在錯綜複雜的狀況中，抽絲剝繭，理清頭緒。理解是邏輯層面的天賦，也是你的天賦，當你面對複雜的狀況，總是能以清晰的角度交代清楚，你所提出的意見，也會增進對事件的理解。切記!!等待邀請到來才發言，否則容易招致不必要的誤解。 `,
  `閘門63:懷疑,科學的、天生多疑的頭腦，質疑每件事情的真實性，渴望找出答案，除非自己找到證據，否則很難相信。會注意並且質疑任何讓人感到不安全的人事物，除非能充分了解並做出評估，否則不會停止質疑。切記!質疑可以指向外在世界，但是不可用在人生或個人選擇上。 `,
  `閘門64:困惑,對於解開過去的事件或議題感到焦慮，要接受困惑是生命的一部分。影像思考，腦中充滿各種畫面，試圖想為自己的經歷找出解答。大量資料在頭腦內循環流動，必須接受困惑這段過程，一旦清晰的答案出現，可能忍不住而貿然行動，但是這些靈光乍現出現的目的是分享，而不是要你行動!!`
