<template>
  <div>
    <el-tabs v-model="level1ActiveName">
      <el-tab-pane label="總覽" name="listAll">
        <ul class="buttons">
          <li
            v-for="(n, k) in 64" :key="k"
          >
            <button
              @click="() => change(k)">{{ n }}</button>
          </li>
        </ul>
        <hr />
        <p class="content">{{ text }}</p>
      </el-tab-pane>
      <el-tab-pane label="輸入" name="input">
        <div>
          <el-input placeholder="Please input" v-model="redBlackInput">
            <template slot="prepend">紅黑</template>
          </el-input>
        </div>
        <div>
          <el-input placeholder="Please input" v-model="redInput">
            <template slot="prepend">紅</template>
          </el-input>
        </div>
        <div>
          <el-input placeholder="Please input" v-model="blackInput">
            <template slot="prepend">黑</template>
          </el-input>
        </div>
        <el-tabs v-model="activeName">
          <el-tab-pane label="紅黑" name="rb">
            <p v-for="(c, k) in getContents(redBlackInput)" :key="k">{{c}}</p>
          </el-tab-pane>
          <el-tab-pane label="紅" name="r">
            <p v-for="(c, k) in getContents(redInput)" :key="k">{{c}}</p>
          </el-tab-pane>
          <el-tab-pane label="黑" name="b">
            <p v-for="(c, k) in getContents(blackInput)" :key="k">{{c}}</p>
          </el-tab-pane>
        </el-tabs>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import list from "../source.js";
export default {
  name: "HelloWorld",
  // props: {
  //   msg: String,
  // },
  methods: {
    getContents(input){
      // var c = ''
      var r = []
      r = input.split(',').map( v => {
        v = parseInt(v)
        let l = list[v - 1] 
        // console.log(l)
        return l
      })
      return r
    },
    change(index) {
      this.active = index
      this.text = list[index]
    },
  },
  data() {
    return {
      level1ActiveName: 'listAll',
      activeName: 'rb',
      redBlackInput: '1,2,3,4,5',
      redInput: '1,2,3,4,5',
      blackInput: '1,2,3,4,5',
      active: 0,
      text: list[0],
    };
  },
};
</script>

